import { useContext, useEffect, useState } from 'react';
import { Redirect } from 'react-router-dom';

import Form, { useForm } from '@miq/form';
import { AdminView } from '@miq/adminjs';
import { addForwardSlash } from '@miq/utils';
import { Icons, IconNavLink, ToastCtx } from '@miq/components';

import './update-view.scss';

import { BackToStaffDocumentsLink } from '../../components';
import DocuFileUploadButton from '../../docufile/UploadButton';
import { DocuFileList } from '../../docufile';
import { docServices } from '../utils';
import {
  DocumentTitleInput,
  DocumentArchiveIconButton,
  DocumentApproveIconButton,
  DocumentDeleteIconButton,
} from '../components';
// import DocumentShareModal from './ShareModal';
import DocumentUpdateForm from './UpdateForm';

const Actions = (props) => {
  const { docSlug, document } = props;

  const { path } = props.match;
  const successUrl = `${addForwardSlash(props.match.url)}view/`;

  return (
    <div className="document-update-view-actions d-flex align-items-center justify-content-between mb-1">
      <div>
        <DocumentDeleteIconButton
          {...props}
          {...{ docSlug }}
          label="Supprimer"
          className="btn-danger-3 me-1"
          onSuccess={() => props.history.push(path.replace(':docSlug/', ''))}
        />
      </div>

      <div className="d-flex">
        <DocumentApproveIconButton
          {...props}
          document={document}
          onSuccess={() => location.reload()}
          className="btn-primary-2 me-2"
        />
        <DocumentArchiveIconButton
          {...props}
          document={document}
          onSuccess={() => props.history.push(successUrl)}
          className="btn-primary-3"
        />
      </div>
    </div>
  );
};

export default function DocumentUpdateView(props) {
  const { docSlug } = props.match.params;

  const [document, setDocument] = useState(null);
  const [error, setError] = useState(null);
  const toast = useContext(ToastCtx);

  const form = useForm({ title: '', reference: '', description: '' });
  const { setValues } = form;

  useEffect(() => {
    docServices
      .detail(docSlug)
      .then((data) => {
        setDocument(data);
        if (data.is_read_only) return;

        setValues({
          title: data.title || '',
          reference: data.reference || '',
          description: data.description || '',
        });
      })
      .catch((err) => {
        if (err.response) {
          if (err.response.status === 404) {
            setError('notfound');
            return;
          }
        }

        setError('error');
      });
  }, [docSlug, setValues]);

  if (error) {
    return (
      <div>
        <h1>Ce document n'existe pas</h1>
      </div>
    );
  }

  if (!document || !document.slug) return null;
  if (document.is_read_only) return <Redirect to={`${addForwardSlash(props.match.url)}view/`} />;

  const handleTextInputSave = ({ name, value }) => {
    if (!document || document.is_read_only) return;
    return docServices.patch(docSlug, { [name]: value }, { [name]: document[name] }).catch((err) => {});
  };

  const addFile = ({ slug }) =>
    docServices
      .patchFile(docSlug, { action: 'add', files: [slug] })
      .then((data) => {
        setDocument({ ...document, ...data });
      })
      .catch((err) => {});

  const removeFile = ({ slug }) =>
    setDocument({
      ...document,
      files: document.files.filter((i) => i !== slug),
      files_data: document.files_data.filter((i) => i.slug !== slug),
    });

  const shared = { ...props, toast, docSlug, document, setDocument, docSlug, form, handleTextInputSave };

  return (
    <Form context={form} className="document-update-form">
      {/* <Prompt when={isL} message="Are you sure?"> */}
      <AdminView
        className="document-update-view miq-container-fluid"
        title={<BackToStaffDocumentsLink className="btn-secondary-4" />}
        actions={
          <IconNavLink
            to={`${addForwardSlash(props.match.url)}share/`}
            // onClick={() => setOpen(true)}
            disabled={document.is_read_only}
            Icon={Icons.Share}
            className="btn-primary"
            label="Partager"
            title="Partager ce document avec des personnes"
          />
        }
      >
        <Actions {...shared} />

        <AdminView.Section>
          <DocumentTitleInput onSave={handleTextInputSave} />
        </AdminView.Section>

        {/* FORM */}
        <AdminView.Section className="mb-3">
          <DocumentUpdateForm {...shared} />
        </AdminView.Section>

        {/* FILES */}

        <AdminView.Section
          title="Fichiers"
          actions={
            <DocuFileUploadButton onSuccess={addFile} disabled={document.is_read_only} className="btn-primary-2" />
          }
        >
          <DocuFileList document={document} items={document.files_data} onFileDelete={removeFile} />
        </AdminView.Section>
      </AdminView>
      {/* </Prompt> */}
    </Form>
  );
}
